.sr-only, .screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .screen-reader-text:active, .sr-only-focusable:focus, .screen-reader-text:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

body {
  font-family: 'Helvetica', Arial, sans-serif;
  font-weight: 100;
  -webkit-font-smoothing: antialiased;
  color: #145363;
  background: #103647; }

.wrap {
  margin-top: 3.75em;
  background: #fff;
  padding-right: 0;
  padding-left: 0;
  padding-bottom: 0.1em; }
  @media (min-width: 768px) {
    .wrap {
      margin-top: 0;
      padding-left: 5%; } }

.content {
  padding: 0 1.25em; }

@media (min-width: 992px) {
  .content, .services, .welcome-content {
    padding-left: 15vw;
    padding-right: 15vw; } }

.js-loading *,
.js-loading *:before,
.js-loading *:after {
  animation-play-state: paused !important; }

